import React from "react"
import styled from "@emotion/styled"
import { Link } from "gatsby"

const PostContainer = styled(Link)`
  width: 90vw;
  max-width: 650px;
  height: 125px;
  margin-bottom: 10px;
  background-color: var(--black);
  color: var(--white);
  text-decoration: none;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 25px;
  @media (min-width: 600px) {
    width: 47.5vw;
    height: 190px;
    font-size: 2.5rem;
    text-align: center;
  }
`

const Post = ({ text, link }) => {
  return <PostContainer to={link}>{text}</PostContainer>
}

export default Post
