import React from "react"
import styled from "@emotion/styled"
import { graphql, useStaticQuery } from "gatsby"
import BackgroundImage from "gatsby-background-image"

import Button from "../commons/Button"

const HeroContainer = styled.section`
  height: 30vh;
  background-color: var(--black);
  color: var(--white);
  border-radius: 0 0 30px 30px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  position: relative;
  .content {
    position: absolute;
    z-index: 2;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    h1,
    h2 {
      margin: 0;
    }
    h2 {
      margin-bottom: 10px;
    }
  }
  > picture {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    z-index: 1;
    animation: animate 8s ease-in-out infinite;
    &.star-0 {
      animation-delay: 0s;
    }
    &.star-1 {
      animation-delay: -1s;
    }
    &.star-2 {
      animation-delay: -2s;
    }
    &.star-3 {
      animation-delay: -3s;
    }
    &.star-4 {
      animation-delay: -4s;
    }
    &.star-5 {
      animation-delay: -5s;
    }
    &.star-6 {
      animation-delay: -6s;
    }
    &.star-7 {
      animation-delay: -7s;
    }
  }
  @keyframes animate {
    0%,
    20%,
    40%,
    60%,
    80%,
    100% {
      opacity: 0;
    }
    10%,
    30%,
    50%,
    70%,
    90% {
      opacity: 1;
    }
  }
  @media (min-width: 600px) {
    height: 50vh;
    .content {
      h1 {
        font-size: 7.2rem;
      }
      h2 {
        font-size: 4.8rem;
      }
    }
  }
`

const ImageBackground = styled(BackgroundImage)`
  width: 100%;
  height: 100%;
`

const Hero = () => {
  const { stars } = useStaticQuery(graphql`
    query {
      stars: allFile(
        filter: {
          sourceInstanceName: { eq: "images" }
          relativePath: { regex: "/stars/" }
        }
      ) {
        nodes {
          relativePath
          sharp: childImageSharp {
            fluid {
              ...GatsbyImageSharpFluid_withWebp
            }
          }
        }
      }
    }
  `)
  return (
    <HeroContainer>
      {stars.nodes.map((el, index) => (
        <picture className={`star-${index}`} key={`star-${index}`}>
          <ImageBackground tag="star" fluid={el.sharp.fluid} />
        </picture>
      ))}
      <div className="content">
        <h1>Erick Mata</h1>
        <h2>Frontend UI Developer</h2>
        <Button
          text="saber ms"
          color="var(--white)"
          background="var(--black)"
          link
          to="/about-me"
        />
      </div>
    </HeroContainer>
  )
}

export default Hero
