import React from "react"
import { graphql, useStaticQuery } from "gatsby"
import styled from "@emotion/styled"

import Project from "./Project"
import Button from "../commons/Button"

const ProjectsContainer = styled.section`
  padding: 25px 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  h2,
  .projects__container {
    margin-bottom: 10px;
  }
  .projects__container {
    a {
      margin-bottom: 10px;
      &:last-child {
        margin-bottom: 0;
      }
    }
  }
  @media (min-width: 600px) {
    h2 {
      font-size: 3.6rem;
    }
    .projects__container {
      display: grid;
      grid-template-columns: 1fr 1fr;
      grid-template-rows: 1fr 1fr;
      grid-gap: 10px;
      a:nth-of-type(1) {
        grid-column: 1 / 2;
        grid-row: 1 / 3;
        height: 400px;
      }
      a:nth-of-type(2) {
        grid-column: 2 / 3;
        grid-row: 1 / 2;
      }
      a:nth-of-type(3) {
        grid-column: 2 / 3;
        grid-row: 2 / 3;
      }
    }
  }
`

const Projects = () => {
  const {
    allDatoCmsProject: { nodes },
  } = useStaticQuery(graphql`
    query {
      allDatoCmsProject(limit: 3) {
        nodes {
          titulo
          slug
        }
      }
    }
  `)

  return (
    <ProjectsContainer>
      <h2>Proyectos destacados</h2>
      <div className="projects__container">
        {nodes.map(({ titulo, slug }, index) => (
          <Project
            key={`Proyecto-${index}`}
            text={titulo}
            link={window.location.pathname === "/" ? "projects/" + slug : slug}
          />
        ))}
      </div>
      {window.location.pathname === "/" && (
        <Button
          text="ver ms"
          color="var(--black)"
          background="var(--white)"
          link
          to="/projects"
        />
      )}
    </ProjectsContainer>
  )
}

export default Projects
