import React from "react"
import styled from "@emotion/styled"
import { Link } from "gatsby"

const ButtonContainer = styled.button`
  width: 150px;
  height: 50px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-family: var(--bold);
  background: transparent;
  border: 2px solid ${props => props.color};
  position: relative;
  a {
    color: ${props => props.color};
    text-decoration: none;
    text-transform: uppercase;
    span {
      position: relative;
      z-index: 2;
      transition: color 0.5s;
      transition-delay: 1.25s;
    }
    &:hover span {
      color: ${props => props.background};
    }
    &::before {
      content: "";
      position: absolute;
      left: -20px;
      top: 50%;
      transform: translateY(-50%);
      width: 20px;
      height: 2px;
      background: ${props => props.color};
      box-shadow: 5px -8px 0 ${props => props.color},
        5px 8px 0 ${props => props.color};
      transition: width 0.5s, left 0.5s, height 0.5s, box-shadow 0.5s;
      transition-delay: 1s, 0.5s, 0s, 0s;
    }
    &:hover::before {
      width: 60%;
      height: 100%;
      left: -2px;
      box-shadow: 5px 0 0 ${props => props.color},
        5px 0 0 ${props => props.color};
      transition-delay: 0s, 0.5s, 1s, 1s;
    }
    &::after {
      content: "";
      position: absolute;
      right: -20px;
      top: 50%;
      transform: translateY(-50%);
      width: 20px;
      height: 2px;
      background: ${props => props.color};
      box-shadow: -5px -8px 0 ${props => props.color},
        -5px 8px 0 ${props => props.color};
      transition: width 0.5s, right 0.5s, height 0.5s, box-shadow 0.5s;
      transition-delay: 1s, 0.5s, 0s, 0s;
    }
    &:hover::after {
      width: 60%;
      height: 100%;
      right: -2px;
      box-shadow: -5px 0 0 ${props => props.color},
        -5px 0 0 ${props => props.color};
      transition-delay: 0s, 0.5s, 1s, 1s;
    }
  }
  @media (min-width: 600px) {
    width: 200px;
    height: 80px;
    font-size: 2.8rem;
  }
`

const Button = ({ text, color, background, to = "#", link = false }) => {
  return (
    <ButtonContainer color={color} background={background}>
      {link ? (
        <Link to={to}>
          <span> {text}</span>
        </Link>
      ) : (
        text
      )}
    </ButtonContainer>
  )
}

export default Button
