import React from "react"
import { graphql, useStaticQuery } from "gatsby"
import styled from "@emotion/styled"

import Post from "./Post"
import Button from "../commons/Button"

const PostsContainer = styled.section`
  padding: 25px 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  h2,
  .posts__container {
    margin-bottom: 10px;
  }
  .posts__container {
    a {
      margin-bottom: 10px;
      &:last-child {
        margin-bottom: 0;
      }
    }
  }
  @media (min-width: 600px) {
    h2 {
      font-size: 3.6rem;
    }
    .posts__container {
      display: grid;
      grid-template-columns: 1fr 1fr;
      grid-template-rows: 1fr 1fr;
      grid-gap: 10px;
      a:nth-of-type(1) {
        grid-column: 1 / 2;
        grid-row: 1 / 2;
      }
      a:nth-of-type(2) {
        grid-column: 1 / 2;
        grid-row: 2 / 3;
      }
      a:nth-of-type(3) {
        grid-column: 2 / 3;
        grid-row: 1 / 3;
        height: 400px;
      }
    }
  }
`

const Posts = () => {
  const {
    allDatoCmsPost: { nodes },
  } = useStaticQuery(graphql`
    query {
      allDatoCmsPost(limit: 3) {
        nodes {
          titulo
          slug
        }
      }
    }
  `)

  return (
    <PostsContainer>
      <h2>Entradas destacadas</h2>
      <div className="posts__container">
        {nodes.map(({ titulo, slug }, index) => (
          <Post
            key={`Post-${index}`}
            text={titulo}
            link={window.location.pathname === "/" ? "blog/" + slug : slug}
          />
        ))}
      </div>
      {window.location.pathname === "/" && (
        <Button
          text="ver ms"
          color="var(--black)"
          background="var(--white)"
          link
          to="blog"
        />
      )}
    </PostsContainer>
  )
}

export default Posts
